@use '../util' as *;

.hero {
    display: grid;
    width: 100%;
    height: rem(820);
    background-color: var(--black);
    grid-template-columns: 1fr rem(540) repeat(3, 1fr);

    @include breakpoint-down(medium) {
        grid-template-columns: 1fr;
        grid-template-rows: 0.7fr 0.1fr 1fr;
        height: rem(992);
        
    }

    @include breakpoint-down(small) {
        height: rem(792);
       
    }
    

    &__logo {
        grid-row: 1;
        grid-column: 2;
        margin-top: rem(65);

        @include breakpoint-down(medium) {
            grid-column: 1;
            grid-row: 2;
            justify-self: center;
            margin-top: 0;
        }
    }

    &__item {
        grid-column: 2;
        grid-row: 1 / 2;
        padding-top: rem(65);
        z-index: 1;

        @include breakpoint-down(medium) {
            justify-self: center;
            grid-column: 1;
            grid-row: 3;
            text-align: center;
            padding-left: 0;
            padding-top: 0;
            width: rem(689);
            
        }

        @include breakpoint-down(small) {
            width: rem(327);
            padding-top: rem(5);
        }

    }

    &__intro {
        padding-top: rem(153);

        @include breakpoint-down(medium) {
            padding-top: rem(32)
        }

        p {
            color: var(--white);
            margin-top: rem(24);
            padding-right: rem(75);

            @include breakpoint-down(medium) {
                padding-inline: rem(58);
            }

            @include breakpoint-down(small) {
                padding-inline: 0;
            }
        }

    }

    &__image {
        grid-area: image;
        background-image: url('../images/homepage/hero-bg-desktop.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center right;
        grid-column: 3 / 6;
        grid-row: 1 / 2;
        height: 100%;
        margin-left: rem(-165);
        

        @include breakpoint-down(medium) {
            background-image: url('../images/homepage/hero-bg-tablet.jpg');
            background-repeat: no-repeat;
            background-size: cover;
            background-position: top center;
            grid-column: 1;
            grid-row: 1;
            margin-left: 0;
        }

        @include breakpoint-down(small) {
            background-image: url('../images/homepage/hero-bg-mobile.jpg');
            background-repeat: no-repeat;
            background-size: cover;
        }
        
    }
        
}