@use '../util' as *;

.booking-form {
    background-color: var(--backgrond);
    position: relative;
    height: rem(320);

    @include breakpoint-down(medium) {
        height: rem(445);
    }

    @include breakpoint-down(small) {
        height: rem(564);
    }

    
    .pattern-curve-bottom-right {
        display: block;
        position: relative;
        width: rem(993);
        height: rem(320);
        background-image: url('../images/patterns/pattern-curve-bottom-right.svg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: top left;
        position: absolute;
        transform: translateX(-10%);

        @include breakpoint-down(medium) {
            display: none;
        }

       
    }

    .form-container {
        width: rem(1110);
        height: 100%;
        margin: 0 auto;
        position: relative;

        @include breakpoint-down(medium) {
            width: rem(689);
        }

        @include breakpoint-down(small) {
            width: rem(327);
        }
        
    }

    .form { 
        width: rem(540);
        position: absolute;
        right: 0;
        bottom: rem(116);
        background: var(--white);
        box-shadow: 0px 75px 100px -50px rgba(56, 66, 85, 0.503223);

        &::before{
            content: '';
            width: rem(160);
            height: rem(76);
            background-image: url('../images/patterns/pattern-lines.svg');
            background-repeat: no-repeat;
            background-size: cover;
            position: absolute;
            bottom: rem(-23);
            left: rem(-80);
    
            @include breakpoint-down(medium) {
                display: none;
            }
        }

        @include breakpoint-down(medium) {
            right: rem(74.5);
            bottom: rem(120);
        }

        @include breakpoint-down(small) {
            width: rem(327);
            bottom: rem(86);
            right: 0;
        }

        &__wrapper {
            padding: rem(48);
            position: relative;

            @include breakpoint-down(small) {
                padding: rem(32);
            }
        }

        input {
            outline: none;
            border: none;
            font-size: rem(18);
            line-height: rem(28);
            letter-spacing: rem(-0.225);
            color: var(--godGray);
            caret-color: var(--beaver);
            cursor: pointer;
        }


        ::placeholder {
            font-size: rem(18);
            line-height: rem(28);
            letter-spacing: -0.225px;
            color: var(--godGray);
            opacity: 0.5;
        }
        
        &__personal-info,
        &__guests-number {
            border-bottom: 1px solid hsl(0, 0%, 56%);
            transition: all 250ms ease-in-out;

            small {
                bottom: rem(-20);
                left: rem(16);
            }
            
        }

        &__personal-info:hover,
        &__guests-number:hover {
            border-bottom: rem(1) solid var(--godGray);
        }

        &__personal-info,
        &__guests-number,
        &__date,
        &__time {
            margin-bottom: rem(32);
            cursor: pointer;
            position: relative;
        }

        &__info {
            width: 90%;
            padding-left: rem(16);
            padding-bottom: rem(17);
        }

        &__date,
        &__time {
            display: flex;
            align-items: center;
            justify-content: space-between;

            @include breakpoint-down(small) {
                display: grid;
                grid-template-rows: 1fr 1fr;
            }

            .date, 
            .time {
                width: 22%;
                padding: 0 rem(28) rem(17) rem(16);
                border-bottom: 1px solid hsla(0, 0%, 56%);

                @include breakpoint-down(small) {
                    grid-row: 2;
                    width: 89%;
                    padding: 0 rem(20) rem(17) rem(14);

                }

                .date:hover,
                .time:hover {
                    border-color:  var(--godGray);
                }
            }

            label {
                line-height: rem(28);

                @include breakpoint-down(small) {
                    grid-row: 1;
                    grid-column: 1 / 3;
                }
                }

            small {
                bottom: 0;

                @include breakpoint-down(small) {
                    bottom: rem(-18);
                }
            }

        }

        &__date {

            .date-year {
                @include breakpoint-down(small){
                width: 100%;
              }
            }
        }

        &__time-options {
            border-bottom: rem(1) solid hsla(0, 0%, 56%);

            &:hover {
                border-bottom: rem(1) solid var(--godGray);
            }

            @include breakpoint-down(small) {
                grid-row: 2;
            }

            .option-selected {
                font-size: rem(18);
                line-height: rem(28);
                letter-spacing: rem(-0.225);
                color: var(--godGray);
                padding: 0 rem(47) rem(17) rem(16);
                background-color: var(--white);
                position: relative;

                &::after {
                    content: '';
                    position: absolute;
                    top: rem(10);
                    right: rem(12);
                    width: rem(16);
                    height: rem(8);
                    background-image: url('../images/icons/icon-arrow.svg');
                    background-repeat: no-repeat;
                    background-size: rem(16) rem(8);
                    transition: all 250ms ease-in-out;
                }
            }

            .time-options-container {
                display: flex;
                flex-direction: column;
                gap: rem(16);
                background-color: var(--white);
                box-shadow: 0px 15px 25px rgba(56, 66, 85, 0.24623);
                position: absolute;
                top: rem(52);
                padding: 16px 30px 15px 16px;
                z-index: 1;
                display: none;
                
            }

            .option {
                display: flex;
                align-items: center;

                input[type="radio"] {
                    visibility: hidden;
                    width: 0;
                    height: 0;
                    margin: 0;
                    cursor: pointer;
                    transition: all 250ms ease-in-out;
                }
        
                input[type="radio"] ~ .check-mark {
                    display: block;
                    width: rem(10.43);
                    height: rem(7.66);
                    background-image: url('../images/icons/icon-check.svg');
                    background-repeat: no-repeat;
                    background-size: rem(10.43) rem(7.66);
                    opacity: 0;
                }
        
                input[type="radio"]:checked ~ .check-mark {
                    opacity: 1;
                }

                label {
                    font-size: rem(18);
                    line-height: rem(28);
                    letter-spacing: rem(-0.225);
                    color: var(--godGray);
                    padding-left: rem(16);
                    cursor: pointer;
                }
            }

            .time-options-container.active + .option-selected::after {
                transform: rotate(180deg);
                top: rem(10);
            }
    
            .time-options-container.active {
                display: block;
            }
        }

        &__guests-number {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0 rem(32) rem(16);
            transition: opacity 250ms ease-in-out;

            .counter__btn {
                background-color: transparent;
                border: none;
            }
            
            img {
                display: block;
                cursor: pointer;
                width: 100%;
            }

            .counter:hover {
                opacity: 0.5;
            }

            h3 {
                text-transform: none;
            }
        }

        .reservation-btn {
            width: 100%;
            background-color: var(--black);
            border: 1px solid var(--black);
            color: var(--white);
            font-family: var(--font-spartan);
            font-size: rem(14);
            font-weight: 600;
            line-height: rem(28);
            letter-spacing: rem(2);
            text-transform: uppercase;
            padding: rem(18) 0;
            cursor: pointer;
            transition: all 250ms ease-in-out;
        }

        .reservation-btn:hover {
            background-color: var(--white);
            color: var(--black);
            
        }

        .reservation-btn.inactive {
            opacity: 0.25;
        }

        &__personal-info.error {
            border-color: hsl(0, 43%, 50%);
        }

        .form-control.error > input {
            color: hsl(0, 43%, 50%);
            border-color: hsl(0, 43%, 50%);
        }

        .form-control.error > input::placeholder {
            color: hsl(0, 43%, 50%);
        }

        small {
            font-size: rem(10);
            font-weight: 500;
            line-height: rem(11.2);
            letter-spacing: rem(-0.13);
            color: hsl(0, 43%, 50%);
            position: absolute;
            visibility: hidden;
        }
        
        .form-control.error small {
            visibility: visible;
            
        }
    
        .form-control.error label {
            color: hsl(0, 43%, 50%);
        }
   
    }

}



    

   
    
    
    


    





