@use '../util' as *;

.feature {
    background-color: var(--background);
    position: relative;

    .pattern-left {
        width: rem(895);
        height: rem(320);
        background-image: url('../images/patterns/pattern-curve-top-right.svg');
        background-repeat: no-repeat;
        background-size: cover;
        position: absolute;
        top: rem(220);

        @include breakpoint-down(medium) {
            top: rem(110);
            width: 50%;
        }

        @include breakpoint-down(small) {
            display: none;
        }
    }

    &__items {
        display: flex;
        flex-direction: column;
        row-gap: rem(120);

        @include breakpoint-down(small) {
            row-gap: rem(80);
        }
    }

    &__item {
        display: flex;
        justify-content: space-between;

        @include breakpoint-down(medium) {
            flex-direction: column;
            align-items: center;
        }
    }

    &__image--place {
        margin-top: rem(-70);
    }

    &__image--container {
        z-index: 1;
        position: relative;

        @include breakpoint-down(medium) {
            order: -1;
        }
    }

    &__image--container::after {
        content: '';
        width: rem(160);
        height: rem(76);
        background-image: url('../images/patterns/pattern-lines.svg');
        background-repeat: no-repeat;
        background-size: cover;
        position: absolute;
        right: rem(-114);
        top: rem(282);
        z-index: 2;

        @include breakpoint-down(medium) {
            top: rem(218);
            right: rem(-50);
        }

        @include breakpoint-down(small) {
            display: none;
        }
    }

    &__image--food {
        margin-bottom: rem(-70);

        @include breakpoint-down(medium) {
            box-shadow: 0px 75px 100px -50px rgba(56, 66, 85, 0.503223);
            margin-bottom: 0;
        }
    }

    &__about {
        margin-top: rem(170);

        @include breakpoint-down(medium) {
            margin-top: rem(56);
        }

        @include breakpoint-down(small) {
            margin-top: rem(48);
        }
    }

    &__item--food {
        @include breakpoint-down(medium) {
            margin-bottom: rem(120);
        }

        @include breakpoint-down(small) {
            margin-bottom: rem(80);
        }
    }

    .pattern-right {
        width: rem(895);
        height: rem(320);
        background-image: url('../images/patterns/pattern-curve-top-left.svg');
        background-repeat: no-repeat;
        background-size: cover;
        position: absolute;
        right: 0;
        bottom: 0;


        @include breakpoint-down(medium) {
            width: 50%;
        }

        @include breakpoint-down(small) {
            display: none;
        }
    }
}
