@use '../util' as *;


.slider {
    background-color: var(--backgrond);
    position: relative;

    .pattern-left {
        width: rem(895);
        height: rem(320);
        background-image: url('../images/patterns/pattern-curve-top-right.svg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: top left;
        position: absolute;
        transform: translateX(-38%);

        @include breakpoint-down(medium) {
            transform: translateX(-50%);
        }

        @include breakpoint-down(small) {
            display: none;
        }
    }

    &__items {
        padding: rem(160) 0;
        position: relative;
        display: grid;
        column-gap: rem(125);
        grid-template-areas: 
            'image content'
            'image button'
            'image toggle' ;

            @include breakpoint-down(medium) {
                padding: rem(120) 0;
                justify-items: center;
                grid-template-areas: 
                    'image'
                    'toggle' 
                    'content'
                    'button';
            }

            @include breakpoint-down(small) {
                padding: rem(80) 0 rem(124) 0;
            }
    }


    &__image {
        position: relative;
        max-width: 100%;
        grid-area: image;
    }

    &__toggle {
        grid-area: toggle;

        @include breakpoint-down(medium) {
            justify-self: normal;
            display: flex;
            justify-content: space-between;
            margin-top: rem(56);
        }

        @include breakpoint-down(small) {
            flex-direction: column;
            gap: rem(20);
            justify-content: center;
            margin-top: rem(48);
        }

        button {
            display: block;
            position: relative;
            background-color: transparent;
            border: none;
            font-family: var(--font-spartan);
            font-size: rem(14);
            font-weight: 600;
            letter-spacing: 2px;
            color: hsl(0, 0%, 30%, 0.5);
            line-height: 1;
            text-transform: uppercase;
            margin-bottom: rem(15);
            transition: color 300ms ease-in-out;
            cursor: pointer;
        }

        button:hover,
        button[aria-selected="true"] {
            color: hsl(0, 0%, 30%);
        }

        button::before {
            content: '';
            display: none;
            position: absolute;
            top: rem(10);
            left: rem(-125);
            width: rem(95);
            height: 1px;
            background-color: var(--beaver);
        }

        button[aria-selected="true"]::before {
            display: block;
        }

        @include breakpoint-down(medium) {
            button::before {
                top: rem(27);
                left: rem(68);
                width: rem(48);
            }
        }

        @include breakpoint-down(small) {
            button::before {
                left: rem(128);
            }
        }

    }

    &__image::before {
        content: '';
        width: rem(160);
        height: rem(76);
        background-image: url('../images/patterns/pattern-lines.svg');
        background-repeat: no-repeat;
        background-size: cover;
        position: absolute;
        top: rem(-38);
        left: rem(-40);
        z-index: 1;

        @include breakpoint-down(small) {
            display: none;
        }
    }

    &__description {
        grid-area: content;
        align-self: end;

        @include breakpoint-down(medium) {
            margin-top: rem(40);
        } 

        @include breakpoint-down(small) {
            margin-top: rem(24);
        }
    }

    &__booking-btn {
        grid-area: button;
    }

}