@use '../util' as *;

.menu {
    background-color: var(--black);
    color: var(--white);

    &__set {
        display: grid;
        grid-template-columns: 0.6fr 1fr;
        gap: rem(125);
        padding-top: rem(200);
        padding-bottom: rem(120);

        @include breakpoint-down(medium) {
            grid-template-columns: 1fr;
            grid-template-rows: 0.5fr 1fr;
            gap: rem(56);
            justify-items: center;
            padding-top: rem(95);
            padding-bottom: rem(95);  
        }

        @include breakpoint-down(small) {
            grid-template-rows: 0.2fr 1fr;
            gap: rem(62);
            padding-top: rem(72);
            padding-bottom: rem(72); 
        }
    }

    &__items {
        padding-top: rem(48);

        @include breakpoint-down(small) {
            padding-top: 0;
        }
    }

    &__item {
        display: flex;
        align-items: center;
        gap: rem(62);

        @include breakpoint-down(small) {
            justify-content: center;
            flex-direction: column;
            gap: rem(32);
            text-align: center;
        }
    }

    &__item-image {
        max-width: 100%;
        position: relative;
    }

    &__item-image::after {
        content: '';
        width: rem(32);
        height: rem(1);
        background-color: var(--beaver);
        position: absolute;
        top: rem(12);
        right: rem(-32);

        @include breakpoint-down(small) {
            display: none;
        }
    }

    &__item-text {

        p {
            font-size: rem(15);
            font-weight: 400;
            line-height: rem(28);

            @include breakpoint-down(small) {
                padding-inline: rem(15);
            }
        }

        h3, p {
            @include breakpoint-down(medium) {
                text-align: left;
            }

            @include breakpoint-down(small) {
                text-align: center;
            }
        }
    }

    .divide {
        display: block;
        width: rem(540);
        height: rem(1);
        background-color: var(--white);
        opacity: 0.15;
        margin-top:  rem(24);
        margin-bottom: rem(24);

        @include breakpoint-down(medium) {
            width: rem(573);
        }

        @include breakpoint-down(small) {
            width: rem(327);
        }
       
    }
}