:root {
  --backgrond: hsl(0, 0%, 95%);
  --beaver: hsl(27, 22%, 51%);
  --godGray: hsl(0, 0%, 7%);
  --mirage: hsl(234, 30%, 13%);
  --ebonyClay: hsl(218, 21%, 18%);
  --shuttleGray: hsl(217, 14%, 42%);
  --white: hsl(0, 0%, 100%);
  --bunker: hsl(219, 21%, 18%);
  --placeholder: hsl(0, 0%, 7, 0.5);
  --black: hsl(0, 0%, 0%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  width: 100%;
  padding: 0;
  margin: 0;
  font-family: var(--font-spartan);
  font-size: 1.125rem;
}

.wrapper {
  width: 69.375rem;
  margin: 0 auto;
}
@media (max-width: 63.9375em) {
  .wrapper {
    width: 35.8125rem;
  }
}
@media (max-width: 39.975em) {
  .wrapper {
    width: 20.4375rem;
  }
}

.wrapper-max {
  width: 69.375rem;
  margin: 0 auto;
}
@media (max-width: 63.9375em) {
  .wrapper-max {
    width: 43.0625rem;
  }
}
@media (max-width: 39.975em) {
  .wrapper-max {
    width: 20.4375rem;
  }
}

.wrapper-pall {
  width: 27.8125rem;
}
@media (max-width: 39.975em) {
  .wrapper-pall {
    width: 100%;
  }
}

:root {
  --font-spartan: "Spartan", sans-serif;
}

h1,
h2,
h3,
p {
  margin: 0;
  padding: 0;
}
@media (max-width: 63.9375em) {
  h1,
h2,
h3,
p {
    text-align: center;
  }
}

h1 {
  font-size: 4rem;
  font-weight: 300;
  line-height: 4.5rem;
  letter-spacing: -0.05rem;
  color: var(--white);
}
@media (max-width: 63.9375em) {
  h1 {
    font-size: 3rem;
    line-height: 4rem;
    letter-spacing: -0.0375rem;
  }
}
@media (max-width: 39.975em) {
  h1 {
    font-size: 2rem;
    line-height: 2.5rem;
    letter-spacing: -0.025rem;
  }
}

h2 {
  font-size: 2.5rem;
  font-weight: 700;
  line-height: 3.5rem;
  letter-spacing: 0.5px;
}
@media (max-width: 39.975em) {
  h2 {
    font-size: 2rem;
    line-height: 2.5rem;
    letter-spacing: -0.4px;
  }
}

h3 {
  font-size: 1.125rem;
  font-weight: 700;
  line-height: 1.75rem;
  letter-spacing: -0.22px;
  text-transform: capitalize;
}

a {
  text-decoration: none;
}

input, button {
  font-family: inherit;
}

.text-intro {
  font-size: 1.125rem;
  line-height: 1.875rem;
  font-weight: 400;
  letter-spacing: -0.01375rem;
  margin-top: 1rem;
}
@media (max-width: 39.975em) {
  .text-intro {
    font-size: 0.9375rem;
    line-height: 1.5625rem;
    letter-spacing: -0.011875rem;
  }
}

img {
  display: block;
}

.box-shadow {
  box-shadow: 0px 75px 100px -50px rgba(56, 66, 85, 0.503223);
}

.decoration {
  display: flex;
  align-items: center;
  gap: 0.3125rem;
  margin-bottom: 2.5rem;
}
@media (max-width: 63.9375em) {
  .decoration {
    justify-content: center;
    margin-bottom: 2rem;
  }
}

.circle {
  border: 0.0625rem solid var(--beaver);
  border-radius: 100%;
  width: 0.4375rem;
  height: 0.4375rem;
}

.line {
  border: 0.0625rem solid var(--beaver);
  width: 3.5rem;
}

@media (max-width: 39.975em) {
  .logo {
    width: 5.15rem;
    height: 2rem;
  }
}

.booking-btn {
  display: block;
  width: 15.3125rem;
  max-height: 4rem;
  background-color: var(--black);
  padding: 1.5rem 3.21875rem;
  border: 1px solid var(--black);
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--white);
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-top: 2rem;
  transition: all 250ms ease-in-out;
}
@media (max-width: 63.9375em) {
  .booking-btn {
    margin: 2rem auto 0 auto;
  }
}

.booking-btn:hover {
  background-color: var(--white);
  color: var(--godGray);
}

.booking-btn--dark {
  border: 1px solid var(--white);
  color: var(--white);
}

.booking-btn--dark:hover {
  color: var(--godGray);
}

.booking-btn--reservation {
  border-color: var(--white);
  background-color: transparent;
}

.hero {
  display: grid;
  width: 100%;
  height: 51.25rem;
  background-color: var(--black);
  grid-template-columns: 1fr 33.75rem repeat(3, 1fr);
}
@media (max-width: 63.9375em) {
  .hero {
    grid-template-columns: 1fr;
    grid-template-rows: 0.7fr 0.1fr 1fr;
    height: 62rem;
  }
}
@media (max-width: 39.975em) {
  .hero {
    height: 49.5rem;
  }
}
.hero__logo {
  grid-row: 1;
  grid-column: 2;
  margin-top: 4.0625rem;
}
@media (max-width: 63.9375em) {
  .hero__logo {
    grid-column: 1;
    grid-row: 2;
    justify-self: center;
    margin-top: 0;
  }
}
.hero__item {
  grid-column: 2;
  grid-row: 1/2;
  padding-top: 4.0625rem;
  z-index: 1;
}
@media (max-width: 63.9375em) {
  .hero__item {
    justify-self: center;
    grid-column: 1;
    grid-row: 3;
    text-align: center;
    padding-left: 0;
    padding-top: 0;
    width: 43.0625rem;
  }
}
@media (max-width: 39.975em) {
  .hero__item {
    width: 20.4375rem;
    padding-top: 0.3125rem;
  }
}
.hero__intro {
  padding-top: 9.5625rem;
}
@media (max-width: 63.9375em) {
  .hero__intro {
    padding-top: 2rem;
  }
}
.hero__intro p {
  color: var(--white);
  margin-top: 1.5rem;
  padding-right: 4.6875rem;
}
@media (max-width: 63.9375em) {
  .hero__intro p {
    padding-inline: 3.625rem;
  }
}
@media (max-width: 39.975em) {
  .hero__intro p {
    padding-inline: 0;
  }
}
.hero__image {
  grid-area: image;
  background-image: url("../images/homepage/hero-bg-desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center right;
  grid-column: 3/6;
  grid-row: 1/2;
  height: 100%;
  margin-left: -10.3125rem;
}
@media (max-width: 63.9375em) {
  .hero__image {
    background-image: url("../images/homepage/hero-bg-tablet.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
    grid-column: 1;
    grid-row: 1;
    margin-left: 0;
  }
}
@media (max-width: 39.975em) {
  .hero__image {
    background-image: url("../images/homepage/hero-bg-mobile.jpg");
    background-repeat: no-repeat;
    background-size: cover;
  }
}

.feature {
  background-color: var(--background);
  position: relative;
}
