}
@media (max-width: 63.9375em) {
  .feature .pattern-left {
    top: 6.875rem;
    width: 50%;
  }
}
@media (max-width: 39.975em) {
  .feature .pattern-left {
    display: none;
  }
}
.feature__items {
  display: flex;
  flex-direction: column;
  row-gap: 7.5rem;
}
@media (max-width: 39.975em) {
  .feature__items {
    row-gap: 5rem;
  }
}
.feature__item {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 63.9375em) {
  .feature__item {
    flex-direction: column;
    align-items: center;
  }
}
.feature__image--place {
  margin-top: -4.375rem;
}
.feature__image--container {
  z-index: 1;
  position: relative;
}
@media (max-width: 63.9375em) {
  .feature__image--container {
    order: -1;
  }
}
.feature__image--container::after {
  content: "";
  width: 10rem;
  height: 4.75rem;
  background-image: url("../images/patterns/pattern-lines.svg");
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  right: -7.125rem;
  top: 17.625rem;
  z-index: 2;
}
@media (max-width: 63.9375em) {
  .feature__image--container::after {
    top: 13.625rem;
    right: -3.125rem;
  }
}
@media (max-width: 39.975em) {
  .feature__image--container::after {
    display: none;
  }
}
.feature__image--food {
  margin-bottom: -4.375rem;
}
@media (max-width: 63.9375em) {
  .feature__image--food {
    box-shadow: 0px 75px 100px -50px rgba(56, 66, 85, 0.503223);
    margin-bottom: 0;
  }
}
.feature__about {
  margin-top: 10.625rem;
}
@media (max-width: 63.9375em) {
  .feature__about {
    margin-top: 3.5rem;
  }
}
@media (max-width: 39.975em) {
  .feature__about {
    margin-top: 3rem;
  }
}
@media (max-width: 63.9375em) {
  .feature__item--food {
    margin-bottom: 7.5rem;
  }
}
@media (max-width: 39.975em) {
  .feature__item--food {
    margin-bottom: 5rem;
  }
}
.feature .pattern-right {
  width: 55.9375rem;
  height: 20rem;
  background-image: url("../images/patterns/pattern-curve-top-left.svg");
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  right: 0;
  bottom: 0;
}
@media (max-width: 63.9375em) {
  .feature .pattern-right {
    width: 50%;
  }
}
@media (max-width: 39.975em) {
  .feature .pattern-right {
    display: none;
  }
}

.menu {
  background-color: var(--black);
  color: var(--white);
}
.menu__set {
  display: grid;
  grid-template-columns: 0.6fr 1fr;
  gap: 7.8125rem;
  padding-top: 12.5rem;
  padding-bottom: 7.5rem;
}
@media (max-width: 63.9375em) {
  .menu__set {
    grid-template-columns: 1fr;
    grid-template-rows: 0.5fr 1fr;
    gap: 3.5rem;
    justify-items: center;
    padding-top: 5.9375rem;
    padding-bottom: 5.9375rem;
  }
}
@media (max-width: 39.975em) {
  .menu__set {
    grid-template-rows: 0.2fr 1fr;
    gap: 3.875rem;
    padding-top: 4.5rem;
    padding-bottom: 4.5rem;
  }
}
.menu__items {
  padding-top: 3rem;
}
@media (max-width: 39.975em) {
  .menu__items {
    padding-top: 0;
  }
}
.menu__item {
  display: flex;
  align-items: center;
  gap: 3.875rem;
}
@media (max-width: 39.975em) {
  .menu__item {
    justify-content: center;
    flex-direction: column;
    gap: 2rem;
    text-align: center;
  }
}
.menu__item-image {
  max-width: 100%;
  position: relative;
}
.menu__item-image::after {
  content: "";
  width: 2rem;
  height: 0.0625rem;
  background-color: var(--beaver);
  position: absolute;
  top: 0.75rem;
  right: -2rem;
}
@media (max-width: 39.975em) {
  .menu__item-image::after {
    display: none;
  }
}
.menu__item-text p {
  font-size: 0.9375rem;
  font-weight: 400;
  line-height: 1.75rem;
}
@media (max-width: 39.975em) {
  .menu__item-text p {
    padding-inline: 0.9375rem;
  }
}
@media (max-width: 63.9375em) {
  .menu__item-text h3, .menu__item-text p {
    text-align: left;
  }
}
@media (max-width: 39.975em) {
  .menu__item-text h3, .menu__item-text p {
    text-align: center;
  }
}
.menu .divide {
  display: block;
  width: 33.75rem;
  height: 0.0625rem;
  background-color: var(--white);
  opacity: 0.15;
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}
@media (max-width: 63.9375em) {
  .menu .divide {
    width: 35.8125rem;
  }
}
@media (max-width: 39.975em) {
  .menu .divide {
    width: 20.4375rem;
  }
}

.slider {
  background-color: var(--backgrond);
  position: relative;
}
.slider .pattern-left {
  width: 55.9375rem;
  height: 20rem;
  background-image: url("../images/patterns/pattern-curve-top-right.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top left;
  position: absolute;
  transform: translateX(-38%);
}
@media (max-width: 63.9375em) {
  .slider .pattern-left {
    transform: translateX(-50%);
  }
}
@media (max-width: 39.975em) {
  .slider .pattern-left {
    display: none;
  }
}
.slider__items {
  padding: 10rem 0;
  position: relative;
  display: grid;
  column-gap: 7.8125rem;
  grid-template-areas: "image content" "image button" "image toggle";
}
@media (max-width: 63.9375em) {
  .slider__items {
    padding: 7.5rem 0;
    justify-items: center;
    grid-template-areas: "image" "toggle" "content" "button";
  }
}
@media (max-width: 39.975em) {
  .slider__items {
    padding: 5rem 0 7.75rem 0;
  }
}
.slider__image {
  position: relative;
  max-width: 100%;
  grid-area: image;
}
.slider__toggle {
  grid-area: toggle;
}
@media (max-width: 63.9375em) {
  .slider__toggle {
    justify-self: normal;
    display: flex;
    justify-content: space-between;
    margin-top: 3.5rem;
  }
}
@media (max-width: 39.975em) {
  .slider__toggle {
    flex-direction: column;
    gap: 1.25rem;
    justify-content: center;
    margin-top: 3rem;
  }
}
.slider__toggle button {
  display: block;
  position: relative;
  background-color: transparent;
  border: none;
  font-family: var(--font-spartan);
  font-size: 0.875rem;
  font-weight: 600;
  letter-spacing: 2px;
  color: rgba(77, 77, 77, 0.5);
  line-height: 1;
  text-transform: uppercase;
  margin-bottom: 0.9375rem;
  transition: color 300ms ease-in-out;
  cursor: pointer;
}
.slider__toggle button:hover,
.slider__toggle button[aria-selected=true] {
  color: #4d4d4d;
}
.slider__toggle button::before {
  content: "";
  display: none;
  position: absolute;
  top: 0.625rem;
  left: -7.8125rem;
  width: 5.9375rem;
  height: 1px;
  background-color: var(--beaver);
}
.slider__toggle button[aria-selected=true]::before {
  display: block;
}
@media (max-width: 63.9375em) {
  .slider__toggle button::before {
    top: 1.6875rem;
    left: 4.25rem;
    width: 3rem;
  }
}
@media (max-width: 39.975em) {
  .slider__toggle button::before {
    left: 8rem;
  }
}
.slider__image::before {
  content: "";
  width: 10rem;
  height: 4.75rem;
  background-image: url("../images/patterns/pattern-lines.svg");
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: -2.375rem;
  left: -2.5rem;
  z-index: 1;
}
@media (max-width: 39.975em) {
  .slider__image::before {
    display: none;
  }
}
.slider__description {
  grid-area: content;
  align-self: end;
}
@media (max-width: 63.9375em) {
  .slider__description {
    margin-top: 2.5rem;
  }
}
@media (max-width: 39.975em) {
  .slider__description {
    margin-top: 1.5rem;
  }
}
.slider__booking-btn {
  grid-area: button;
}

.reservation {
  background-image: url("../images/homepage/ready-bg-desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.reservation__text {
