@use '../util' as *;

h1, 
h2, 
h3,
p {
    margin: 0;
    padding: 0;

    @include breakpoint-down(medium) {
        text-align: center;
    }
}

h1 {
    font-size: rem(64);
    font-weight: 300;
    line-height: rem(72);
    letter-spacing: rem(-0.8);
    color: var(--white);

    @include breakpoint-down(medium) {
        font-size: rem(48);
        line-height: rem(64);
        letter-spacing: rem(-0.6);
    }

    @include breakpoint-down(small) {
        font-size: rem(32);
        line-height: rem(40);
        letter-spacing: rem(-0.4);
    }

}

h2 {
    font-size: rem(40);
    font-weight: 700;
    line-height: rem(56);
    letter-spacing: .5px;
    
    @include breakpoint-down(small) {
        font-size: rem(32);
        line-height: rem(40);
        letter-spacing: -0.4px;
    }
}

h3 {
    font-size: rem(18);
    font-weight: 700;
    line-height: rem(28);
    letter-spacing: -0.22px;
    text-transform: capitalize;
}

a {
    text-decoration: none;
}

input, button {
    font-family: inherit;
}

.text-intro {
    font-size: rem(18);
    line-height: rem(30);
    font-weight: 400;
    letter-spacing: rem(-0.22);
    margin-top: rem(16);

    @include breakpoint-down(small) {
        font-size: rem(15);
        line-height: rem(25);
        letter-spacing: rem(-0.19);
    }
}


