@use '../util' as *;

img {
    display: block;
}

.box-shadow {
    box-shadow: 0px 75px 100px -50px rgba(56, 66, 85, 0.503223);
}

.decoration {
    display: flex;
    align-items: center;
    gap: rem(5);
    margin-bottom: rem(40);

    @include breakpoint-down(medium) {
        justify-content: center;
        margin-bottom: rem(32);
    }
}

.circle {
    border: rem(1) solid var(--beaver);
    border-radius: 100%;
    width: rem(7);
    height: rem(7);
}

.line {
    border: rem(1) solid var(--beaver);
    width: rem(56);
}

.logo {

    @include breakpoint-down(small) {
            width: rem(82.4);
            height: rem(32);
        }
}

.booking-btn {
    display: block;
    width: rem(245);
    max-height: rem(64);
    background-color: var(--black);
    padding: rem(24) rem(51.5);
    border: 1px solid var(--black);
    font-size: rem(14);
    font-weight: 600;
    color: var(--white);
    text-transform: uppercase;
    letter-spacing: 2px;
    margin-top: rem(32);
    transition: all 250ms ease-in-out;

    @include breakpoint-down(medium) {
        margin: rem(32) auto 0 auto;
    }
}

.booking-btn:hover {
    background-color: var(--white);
    color: var(--godGray);
}

.booking-btn--dark {
    border: 1px solid var(--white);
    color: var(--white);
}

.booking-btn--dark:hover {
    color: var(--godGray);
}

.booking-btn--reservation {
    border-color: var(--white);
    background-color: transparent;
}

