@use '../util' as *;

html {
    font-size: 100%;
    box-sizing: border-box;
}

*, *::before, *::after {
    box-sizing: border-box;
}

body {
    width: 100%;
    padding: 0;
    margin: 0;
    font-family: var(--font-spartan);
    font-size: rem(18);
}

.wrapper {
        width: rem(1110);
        margin: 0 auto;

        @include breakpoint-down(medium) {
            width: rem(573);
        }

        @include breakpoint-down(small) {
            width: rem(327);
        }
    }

.wrapper-max {
    width: rem(1110);
        margin: 0 auto;

        @include breakpoint-down(medium) {
            width: rem(689);
        }

        @include breakpoint-down(small) {
            width: rem(327);
        }
}

.wrapper-pall {
    width: rem(445);

    @include breakpoint-down(small) {
        width: 100%;
    }
}



